*{
  border:0;
  margin:0;
  font-family: "Roboto", sans-serif;
  box-sizing: border-box;
  line-height: 50px;
  color:#ffffff;
}

html{
  scroll-behavior: smooth;
}

a{
  text-decoration: none;
  color:white;
}

textarea{
  resize:none;
}



body{
  background-color: #19191F;
}



li{
  list-style-type: none;
}

header a {
  text-decoration: none;
  color:#058ED9;
  margin: 20px;
}

header li{
  font-weight:bold;
}


.header{
  display:flex;
  align-items: center;
  justify-content: space-between;
  padding:20px;
  position:fixed;
  width:100%;
  z-index:500;
  transition:0.9s;
}

.header.scroll{
  background:rgba(25,25,31,0.9);
  backdrop-filter:blur(1px);
  transition: 0.9s;
}




.name{
  color:#058ED9;
}


.navbar{
  position: fixed;
  right:0;
}





.navbar > ul{
  display:flex;
  gap:3%;
  flex:1;
  justify-content: flex-end;
 
}


.hero-section{
  width: 100%;
  height:800px;
  
  position: relative;
  display:flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  gap:20%;
}

.hero-text-container{
  margin-left:10%;
}

.svg-hero{
  margin-right:10%;
}

.hero-text ,.hero-text > *:not(h1){
  font-size:26px;
  line-height:5rem;
}


.hero-button{
  background-color: transparent;
  border:2px solid #F4EBD9;
  font-size:1.2rem;
  border-radius:5px;
  cursor: pointer;
  margin-top:10px;
  padding:10px;
}


.banner-text{
  color:white;
  text-align: center;
}

.contact-button{
  background-color: transparent;
  border: 2px solid #aecbda;
  color:#aecbda;
  padding:10px;
  font-size:1.09rem;
  cursor: pointer;
  transition:.5s;
}

.contact-button:hover{
  background-color: #70848e;
  transition:.5s;
}

.about, .experience, .projects, .skills, .contact{
  display:block;
  width:50%;
  margin-left:auto;
  margin-right: auto;
  margin-top:100px;
  color:white;
  
  
}

.experience, .projects, .skills, .contact{
  padding-top:50px;
  border-top:2px;
  border-style: solid;
  border-color: #6B6969;
}

.footer{
  display:block;
  width:100%;
  margin-top:100px;
  height:100%;
}

.about > h1, .experience > h1, .projects > h1,.skills > h1, .contact > h1{
  font-size:1.5rem;
  margin-bottom:20px;
}


.about-photo{
  height:auto;
  width:50%;
  min-width:100px;
  border-radius: 10%;
  
  text-align: center;
  
}

.about-info{
  display:flex;
  flex-direction: row;
}

.about-info-text > li{
  
  font-size:  1.3vw;
  font-weight: bold;
  white-space:nowrap;
}

.experience{
  padding:20px;
  font-size: 1.;
}




.experience-table{
  display:flex;
  flex-direction: column;
  gap:50px;
  
}





.experience-table > div {
  display:flex;
  justify-content: space-between;
  
}

.experience-table > div > *{
  flex: 0 0 75%;
}

.experience-date{
  font-size:1.2rem;
}

.experience-title{
  font-weight:bold;
}

.company{
  font-style:italic;
}

.responsibilities > li{
  list-style-type:disc;
}

.project-container{
  display:flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  gap:20px;
}

.project-card{
  width:500px;
 height:500px;
 background-size: cover;
 background-repeat: no-repeat;
 flex: 0 0 100%;
 display: flex;
 justify-content:center;
 align-items: center;
}

.project-name{

  font-size:1.4rem;
  font-weight:bold;
  display:none;
  
}

.project-card:hover > .project-name{
 display: block;
}

.contact-form{
  display:flex;
  flex-direction: column;
  gap:50px;
}

.form-control{
  display:flex;
  flex-direction: column;
}

.form-control > input, .form-control > textarea {
  border-radius: 5px;
}

.email-input{
  font-size:1.2rem;
}

.email-body{
  font-size:1rem;
  line-height:15px;
  height:200px;
}

.submit-contact{
  background-color: transparent;
  border: 2px solid #6B6969;
  width:25%;
  margin-left:auto;
  margin-right:auto;
  font-size:1.09rem;
  color:white;
  cursor: pointer;
  transition: .5s;
  min-width:100px;
}

.submit-contact:hover{
  background-color: #70848e;
  transition: .5s;
}

.footer{
  width:75%;
  display:block;
  margin-left:auto;
  margin-right:auto;
}

.footer-nav{
  border-top:2px;
  border-bottom:2px;
  border-style: solid;
  border-color:#6B6969;
}

.footer-nav > ul{
  display:flex;
  justify-content: center;
  gap:10%;
  flex-wrap: wrap;
  padding:30px;
}

.sites{
  margin-top:30px;
  display:flex;
  justify-content: space-evenly;
}

.copyright{
  margin-top:20px;
  text-align: center;
  color:#ffffff;
}

.mobile-nav{
  display:none;
  
}


.hamburger-button{
  display:block;
  position: relative;
width:50px;
cursor: pointer;
appearance: none;
background: none;
border:none;
outline: none;
}



.web-developer{
  border-right: .3rem solid #058ED9;
  overflow:hidden;
  margin:0 auto;
  display:inline-block;
  white-space: nowrap;
  vertical-align: middle;
  word-break: break-all;

 animation:
    typewriter 5s steps(10, end) infinite ,
    blink-caret .5s step-end infinite;

}




@keyframes typewriter{
  0%{
    width:0;
  }

  10%{
    width:0;
  }

  50%{
    width:190px;
  }

  80%{
    width:190px;
  }
  90%, 99%{
    width:0;
  }

  100%{
    width:0;
  }




}

@keyframes blink-caret{
  from, to{
    border-color: transparent;
  }

  50%{
    border-color: #058ED9;
  }
}



@media(max-width:1385px){
  .about-info{
      flex-direction: column;
      align-items: center;
      justify-content: center;
  }

  .about-info-text{
    text-align: center;
    
  }
}

@media(max-width:800px){
  
  .hamburger-button{
    display:block;
    margin-left:auto;
    margin-right:10px;
    z-index:101;
  }

  .hamburger-button .burger-bar, .hamburger-button::after, .hamburger-button::before{
    content: '';
    display:block;
    width:100%;
    height:3px;
    background-color: #ffffff;
    margin: 7px;
    transition: 0.4s;
    
  }

  .hamburger-button.active::before{
    transform: rotate(-45deg) translate(-9px, 6px);
  }

  .burger-bar{
    opacity:1;
  }

  .hamburger-button.active::after{
    transform: rotate(45deg) translate(-8px, -4.5px);
  }

  .navbar{
    display:none;
  }

  .mobile-nav{
    position:fixed;
    top:0;
    right:0;
    left:100%;
    margin-right:20px;
    z-index:100;
    min-height:100vh;
    display:block;
    width:100%;
    background-color: #19191F;
    background:rgba(25,25,31,0.9);
    backdrop-filter: blur(1px);
    transition:0.4s;
  }

.mobile-nav > ul{
  margin: 150px 0 0 -25px;
  display: flex;
  flex-direction: column;
  gap:20px;
}

  .mobile-nav.active{
    left:65%;
    transition:0.4s;
  }

  .about > h1, .experience > h1, .projects > h1,.skills > h1, .contact > h1{
    text-align: center;
  }

  .about{
    text-align: center;
   }
}

@media(max-width: 1000px){
  .experience{
    font-size:3vw;
  }

 .about-info-text > li{
  font-size:3vw;
 } 

 .svg-hero{
  display:none;
 }


 .hero-section{
  text-align: center;
 }
 .hero-text-container
 {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
 }

}


@media(max-width:1385px)
{
  .about-info-text{
    padding:0;
  }
}




